<template>
  <div class="home">
    <button @click="exportcode">导出p5代码</button>
    <v-swatches
      v-model="color"
      popover-x="left"
      swatches="text-advanced"
    ></v-swatches>
    <vue-slider v-model="Weight" />
    <br />
    <button
      type="button"
      v-clipboard:copy="code"
      v-clipboard:success="onCopy"
      v-clipboard:error="onError"
    >
      复制代码
    </button>
    <br /><br />

    <button @click="Redraw">重新绘制</button>

    <a href="https://editor.p5js.org/" target="_blank">去p5遍编辑器</a>
    <br /><br /><br /><br /><br /><br /><br /><br />
    {{ code }}
    <vue-p5 v-on="{ setup, draw, keypressed }"></vue-p5>
    <br /><br />
  </div>
</template>

<script>
// @ is an alias to /src

import VueP5 from "vue-p5";
import VSwatches from "vue-swatches";

// Import the styles too, typically in App.vue or main.js
import "vue-swatches/dist/vue-swatches.css";

import VueSlider from "vue-slider-component";
import "vue-slider-component/theme/antd.css";

import store from "store2";
var debounce = require("debounce");

var sketch_;
export default {
  name: "Home",
  components: {
    VueP5,
    VSwatches,
    VueSlider,
  },
  data() {
    return {
      printList: [],
      code: "",
      color: "#1CA085",
      savecolor: "",
      Weight: 10,
      saveWeight: "",
      isRedraw: false,
      createCanvas: {
        with: "",
        height: "",
        background: "",
      },
    };
  },
  mounted() {
    var data = store("p5Picture");
    console.log(data);
    this.$data.printList = data || [];
    this.$data.isRedraw = true;
  },

  methods: {
    Redraw: function (sketch) {
      sketch_.clear();
      sketch_.background("green");
      this.$data.isRedraw = true;
    },
    onCopy: function () {
      alert("You just copied");
    },
    onError: function () {
      alert("Failed to copy texts");
    },
    exportcode() {
      var setupcode = "",
        drawcode = "";
      setupcode = `
          createCanvas(${this.$data.createCanvas.with}, ${this.$data.createCanvas.height});
          background('${this.$data.createCanvas.background}');
       `;
      this.$data.printList.forEach((item) => {
        if (item.ptype == "stroke") {
          drawcode += `
           strokeWeight(${item.Weight});
           stroke('${item.Color}');
           `;
        }
        if (item.ptype == "line") {
          drawcode += `
           line(${item.pmouseX}, ${item.pmouseY}, ${item.mouseX}, ${item.mouseY});

           `;
        }
      });

      var code = `
            function setup() {
                 ${setupcode}
            }

            function draw() {
                ${drawcode}
            }

                  `;
      console.log(code);
      this.$data.code = code;
    },
    setup(sketch) {
      sketch_ = sketch;
      sketch.createCanvas(800, 800);
      sketch.background("green");
      this.$data.createCanvas = {
        with: 800,
        height: 800,
        background: "green",
      };

      sketch.strokeWeight(this.$data.Weight);
      sketch.stroke(this.$data.color);
      this.$data.printList.push({
        ptype: "stroke",
        Weight: this.$data.Weight,
        Color: this.$data.color,
      });
    },
    draw(sk) {
      // draw a line between the previous
      // and the current mouse position
      if (this.$data.isRedraw == false) {
        //手工绘制

        if (this.$data.savecolor != this.$data.color) {
          sk.strokeWeight(this.$data.Weight);
          sk.stroke(this.$data.color);
          this.$data.savecolor = this.$data.color;
          this.$data.printList.push({
            ptype: "stroke",
            Weight: this.$data.Weight,
            Color: this.$data.color,
          });
        }
        if (this.$data.saveWeight != this.$data.Weight) {
          sk.strokeWeight(this.$data.Weight);
          sk.stroke(this.$data.color);
          this.$data.saveWeight = this.$data.Weight;
          this.$data.printList.push({
            ptype: "stroke",
            Weight: this.$data.Weight,
            Color: this.$data.color,
          });
        }
        if (
          sk.mouseIsPressed === true &&
          sk.pmouseX > 0 &&
          sk.pmouseY < 800 &&
          sk.mouseX > 0 &&
          sk.mouseY < 800
        ) {
          sk.line(sk.pmouseX, sk.pmouseY, sk.mouseX, sk.mouseY);
          console.log(sk.pmouseX, sk.pmouseY, sk.mouseX, sk.mouseY);
          this.$data.printList.push({
            ptype: "line",
            pmouseX: sk.pmouseX,
            pmouseY: sk.pmouseY,
            mouseX: sk.mouseX,
            mouseY: sk.mouseY,
          });
        }
      } else {
        //路径还原
        this.$data.printList.forEach((skData) => {
          if (skData.ptype == "line") {
            sk.line(
              skData.pmouseX,
              skData.pmouseY,
              skData.mouseX,
              skData.mouseY
            );
          }
          if (skData.ptype == "stroke") {
            sk.strokeWeight(skData.Weight);
            sk.stroke(skData.Color);
          }
        });
        this.$data.isRedraw = false;
      }
    },
    keypressed(sk) {
      // convert the key code to it's string
      // representation and print it
      const key = String.fromCharCode(sk.keyCode);
      sk.print(key);
    },
  },
  watch: {
    printList: debounce(function (data) {
      console.log(data);
      store("p5Picture", data);
    }, 100),
  },
  render(h) {
    return h(VueP5, { on: this });
  },
};
</script>
<style lang="less" scoped>
.p5Canvas {
  width: 500px;
  height: 500px;
}
</style>
